import React, { PureComponent } from "react";
import { isValidAuthorImage, TWITTER_LOGO_FN } from '../../../../utils';
import {convertHtmlStringToRender} from '../../../Components/customFunction'
import { themePostTracking } from '../../../../../../../../../../actions/themeActions/themeActions';
import moment from 'moment';
import AuthorAvatar from '../../Elements/AuthorAvatar'
import Time from "../../Elements/Time";
import { TWITTER_LOGO } from "../../../../../../../../../../actions/api";
const checkStringInNotNull = (string) => string != null && string !== "" && string !== 0 ? true : false;

export default class AuthorDetails extends PureComponent {
  state = { isAuthorImageValid: true }
  componentDidMount() {
    const { item } = this.props;
    const isAuthorImagevalid = isValidAuthorImage(item.author.picture);
    this.setState({ isAuthorImagevalid: isAuthorImagevalid });
  }

  render() { 
    const { wall, item, network } = this.props;
    const isCta = checkStringInNotNull(item.cta); 
    const postAuthor = wall.Personalization.postAuthor;
    const tb_authorBgColor = { backgroundColor: wall.ThemeRule.authorColor };
    const tb_authorColor = { color: wall.ThemeRule.authorColor };
    const isShopIcon = (item.ugc_products && item.ugc_products.length > 0 && item.hotspot === 0) ? true : false;  
    const networkIcon = network.icon.replace('fa-', '');
    const postTime = wall.Personalization.postTime;
    const networkColor = wall.ThemeRule.iconType == 505 ? network.color : wall.ThemeRule.iconColor; 
    //const iconSize =wall && wall.ThemeRule && wall.ThemeRule.fontSize ? wall.ThemeRule.fontSize :'40px'
    return (
      <div className="tb_st_author_wrapper">
        {isShopIcon ?
        <div className="ts_st_shop_icon">
           <div className="tb__icon tb-shopping-bag ts_shopping_hover" ><div></div></div>
            <div className="tb_st_shop_hover">View Product</div>
          </div>:''}
          <div className="tb_st_social_">
          <div className={`tb__icon tb-${networkIcon}`} style={{ color: networkColor }}><div></div></div>
          </div> 
         {!item.author.isInstaUser ?
        <div className="tb_st_author">
            {postAuthor ?  <div className="tb_st_author_profile"> 
                        <img loading="lazy" src={item.author.picture} onError={(e)=>{e.target.onerror = null;  e.target.src=item.author.errorPic}} alt="image"/> </div> :''}
            <div className="tb_st_author_info">
              {postAuthor ? <div className="tb_st_authorname" style={tb_authorColor}>@{item.author.name}</div> : ''}
              <div className="tb_st_post_info">
                  {postAuthor ? <div className="tb_st_username" style={tb_authorColor}>@{item.author.username}</div> : ''}
                  {postTime && postAuthor ? <div className="tb_st_seprator" style={tb_authorBgColor}><div></div></div>:''}
                  {postTime ? <Time postTime={item.createdAt} timeClass={'tb_sc_time'} timeStyle={tb_authorColor}/> :''}
                  
              </div>
            </div>
        </div>:''}
      </div>
    );
  }
}