import React, { PureComponent } from "react";
import Card from "./elements/Card";
import { connect } from 'react-redux';
import { getDataNextSteps } from '../../../../../../../../../actions/themeActions/themeActions'
import { Swiper, SwiperSlide } from 'swiper/react';
import { EffectCoverflow, Autoplay, Pagination, Navigation } from 'swiper';
import { checkPreview } from "../../../utils";
import 'swiper/css';


function SampleNextArrow(props) {
  const { classNameCustom, onClick, windowWidth, items } = props;
  let itemWidth = 100 / items;
  itemWidth = -itemWidth;
  var windowWidths = windowWidth / 1000 + 0.8;
  return (
    <div className={classNameCustom} style={{ width: 'clamp(1.5rem, ' + windowWidths + 'rem, 3.5rem)', 'right': itemWidth - 2 + '%' }} onClick={onClick} >
      <div className="swArrow_inner">
        <div className="tb__icon tb-chevron-right"><div></div></div>
      </div>
    </div>
  );
}

function SamplePrevArrow(props) {
  const { classNameCustom, style, onClick, windowWidth, items } = props;
  let itemWidth = 100 / items;
  itemWidth = -itemWidth;
  var windowWidths = windowWidth / 1000 + 0.8;
  return (
    <div className={classNameCustom} style={{ width: 'clamp(1.5rem, ' + windowWidths + 'rem, 3.5rem)', 'left': itemWidth - 2 + '%' }} onClick={onClick}>
      <div className="swArrow_inner">
        <div className="tb__icon tb-chevron-left"><div></div></div>
      </div>
    </div>
  );
}
class StoryTheme extends PureComponent {

  state = { cardCount: null, windowWidth: document.querySelector('.taggshopContainer') ? document.querySelector('.taggshopContainer').clientWidth : '', }


  onUpdateData = () => { setTimeout(() => this.requestData(), 1000) }

  requestData = () => {
    const { wall, postData, appendData, hasMoreData, loaderData } = this.props;
    const postCount = wall.ThemeRule.numberOfPosts;
    const timeStamp = Math.floor(Date.now() / 1000);
    if (hasMoreData && !loaderData.isShowMoreLoading) this.props.getDataNextSteps(wall.Wall.id, timeStamp, postCount, appendData.networkID, appendData.after, appendData.heightEvent);
  }

  componentDidUpdate(prevProps, prevState) { 
    if (prevProps.wall?.ThemeRule?.numberOfCoumn !== this.props.wall?.ThemeRule?.numberOfCoumn) {
      this.forceUpdate();
    }
  }

  render() {
    const { appendData, postData, newPostData, completeDataObject, adjustWidth, hasMoreData, wall, webFilters, languageSetting, loaderData } = this.props;
    const { windowWidth, onResponsive } = this.state;
    const columnCount = wall.Personalization.numberOfCoumn;
    const mobileColumn = wall.ThemeRule.mobileColumn;
    const slidePost = wall.ThemeRule.slidePost;
    let themeWidth = columnCount >= 7 ? "100%" : columnCount === 5 && windowWidth > 767 ? "90%" : columnCount === 3 && windowWidth > 767 ? "70%" : "100%"
    let arrowClass = windowWidth > 767 ? (columnCount == 9 || windowWidth > 1600 ? `_17` : columnCount == 7 || windowWidth > 1400 ? `_20` : columnCount == 5 || windowWidth > 1024 ? `_25` : `_34`) : (mobileColumn == 9 ? `_17` : mobileColumn == 7 ? `_20` : mobileColumn == 5 ? `_25` : mobileColumn == 3 ? `_34` : `_90`);

    return (
      <div className={`tb_st_post_container ${checkPreview() ? `checkPreview` : ''}`} id={window.isEditor ? 'isEditorCls ts_theme_container' : 'ts_theme_container'}>
        <SamplePrevArrow classNameCustom={`sw_slider_prev_arrow ts_st_left_arrow${arrowClass}`} />
        <Swiper
          style={{ width: themeWidth }}
          autoHeight={true}
          speed={500}
          // spaceBetween={-500}
          loop="false"
          navigation={{ prevEl: `.sw_slider_prev_arrow`, nextEl: `.sw_slider_next_arrow` }}
          effect={"coverflow"}
          grabCursor={true}
          centeredSlides={true}
          slidesPerView={columnCount == 9 ? 6 : columnCount == 7 ? 5 : columnCount == 5 ? 4 : columnCount == 3 ? 3 : 6}
          coverflowEffect={{
            rotate: 0,
            stretch: columnCount >= 5 ? 0 : columnCount >= 3 ? -6 : 0,
            depth: columnCount >= 5 ? 15 : 10,
            modifier: 10,
            slideShadows: false
          }}
          autoplay={{ delay: slidePost ? wall.ThemeRule.slideDuration * 1000 : 99000, disableOnInteraction: false, pauseOnMouseEnter: true, }}
          breakpoints={{
            320: {
              slidesPerView: mobileColumn == 9 ? 6 : mobileColumn == 7 ? 5 : mobileColumn == 5 ? 4 : mobileColumn == 3 ? 3 : 1,
              coverflowEffect: {
                stretch: mobileColumn >= 5 ? 0 : mobileColumn >= 3 ? -2 : -6,
                depth: mobileColumn >= 5 ? 10 : mobileColumn == 3 ? 15 : 15,
              }
            },
            576: {
              slidesPerView: mobileColumn == 9 ? 6 : mobileColumn == 7 ? 5 : mobileColumn == 5 ? 4 : mobileColumn == 3 ? 3 : 1,
              coverflowEffect: {
                stretch: mobileColumn >= 5 ? 0 : mobileColumn >= 3 ? -4 : -6,
                depth: mobileColumn >= 5 ? 10 : mobileColumn == 3 ? 15 : 15,
              }
            },
            767: {
              slidesPerView: columnCount == 9 ? 6 : columnCount == 7 ? 5 : columnCount == 5 ? 4 : columnCount == 3 ? 3 : 3,
              coverflowEffect: {
                stretch: columnCount >= 5 ? 0 : columnCount >= 3 ? -2 : -6,
                depth: columnCount >= 5 ? 15 : columnCount >= 3 ? 10 : 15,
              }
            },
            1024: {
              slidesPerView: columnCount == 9 ? 6 : columnCount == 7 ? 5 : columnCount == 5 ? 4 : columnCount == 3 ? 3 : 4,
              coverflowEffect: {
                stretch: columnCount >= 5 ? 0 : columnCount >= 3 ? -3 : 0,
                depth: columnCount >= 5 ? 15 : columnCount >= 3 ? 10 : 15,
              }
            },
            1400: {
              slidesPerView: columnCount == 9 ? 6 : columnCount == 7 ? 5 : columnCount == 5 ? 4 : columnCount == 3 ? 3 : 5,
              coverflowEffect: {
                stretch: columnCount >= 5 ? 0 : columnCount >= 3 ? -4 : 0,
                depth: columnCount >= 5 ? 15 : columnCount >= 3 ? 10 : 16,
              }
            },
            1600: {
              slidesPerView: columnCount == 9 ? 6 : columnCount == 7 ? 5 : columnCount == 5 ? 4 : columnCount == 3 ? 3 : 6,
              coverflowEffect: {
                stretch: columnCount >= 5 ? 0 : columnCount >= 3 ? -6 : 0,
                depth: columnCount >= 5 ? 15 : columnCount >= 3 ? 10 : 15,

              }
            },
          }}

          modules={[EffectCoverflow, Autoplay, Pagination, Navigation]} className="ts_st_mySwiper">
          {(newPostData && newPostData.length > 0) ? newPostData.map((item, index) => {
            const cardData = completeDataObject[item];
            return <SwiperSlide key={`poid_${index}`}>
              <Card itemData={cardData} key={`post_Id_${index}`} itemIndex={index} adjustWidth={adjustWidth} {...this.props} />
            </SwiperSlide>
          }) : null
          }
        </Swiper>
        <SampleNextArrow classNameCustom={`sw_slider_next_arrow ts_st_right_arrow${arrowClass}`} onClick={this.onUpdateData} hasMoreData={this.hasMoreData} />
      </div>
    );
  }
}



const mapStateToProps = state => {
  const { appData } = state;
  return {
    appData: appData,
    loaderData: state.loaderData,
    newPostData: appData.newPostData,
    appendData: appData.postData.appendData,
    hasMoreData: appData.postData.hasMoreData[0] ? appData.postData.hasMoreData[0].hasMoreData == false ? false : appData.postData.hasMoreData[appData.postData.appendData.networkID] ? appData.postData.hasMoreData[appData.postData.appendData.networkID].hasMoreData : true : true
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow)),

  }
}
export default connect(mapStateToProps, mapDispatchToProps)(StoryTheme);