import React, { PureComponent } from "react";
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, IS_SCREEN_SHOT, marginTop_mainDiv } from '../../../../utils'
import HotSpot from "../../Elements/HotSpot";


export default class Image extends PureComponent {
  componentDidMount(){
    marginTop_mainDiv()
  }
  render() {
    const { ImageClass, ImageUrl, item, width, height, wall, ugc_products} = this.props
    const ImageBg = { backgroundImage: `url(${ImageUrl})` };
    const styleImg ={ height: item.hotspot ? `auto` :`100%`, width: item.hotspot  ? `100%`:`auto`}
    const styleDiv ={ height: item.hotspot ? `auto` :`100%`, width: item.hotspot  ? `100%`:`100%`}
    return (<>
        <div className="ts_st_image_blur_bg" style={ImageBg}><div></div></div>
       <div className={item.hotspot ? `tb_st_image_hotspot`:ImageClass } style={styleDiv}>

           <img loading="lazy"   src={ImageUrl} data-src={ImageUrl} data-is-optmized="0" data-link={item.link} data-load="0" data-wall-id={wall.Wall.id} data-item-id={item.id} onLoad={this.onLoadImage} data-filter-id={item.filterId} data-stories={item.stories} data-network={item.networkId} width={width} className={`imgID${item.id}`} onError={(e) => { if(item.networkId !== `28`) IMG_LOAD_AFTER_COMPLETE_LAYOUT(e) }} height={height}  style={styleImg} alt="image"/>

       {item.hotspot ? <HotSpot ugc_products={ugc_products} UgcSetting={item.ugc_personalizaion.UgcSetting} item={item}/> :<div></div> }
       {item.type==3 || item.type==5 ? <img loading="lazy" className="ts_st_playIcon" src="https://cloud.taggbox.com/embed-build/commerce/assets/images/play.svg" alt="Play" /> :''}
       </div> 

    </>);
  }
}